<template>
  <div class="content">    
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100">
        <div class="centered-container">
          <h3 v-if="ifNothing"><strong>...</strong></h3>
        </div>
      </div>
      <div v-if="!ifNothing" class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100" >
        <md-card class="md-card-plain">
          <hr>
          <md-card-content>
            <p v-show="1===2">{{ transactions[0].status }}</p>
            <SimpleTable_tr :transactions="transactions"></SimpleTable_tr>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import SimpleTable_tr from "../components/Tables/SimpleTable_tr.vue";

export default {
  components: {
    SimpleTable_tr,
  },
  data() {
    return {
      transactions: [],
      timer: null,
      ifNothing: false,
    };
  },
  mounted() {
    this.fetchData();
    this.timer = setInterval(this.fetchData, 10000);
  },
  methods: {
    fetchData() {
      this.$axios.get('/api/v1/market/list')
        .then(response => {
          if (response.data.message === "error") {
            this.$toast.error(''+response.data.error);
          }
          if (response.data.data) {
            try {
              this.transactions = JSON.parse(response.data.data); //  JSON
            } catch (error) {
              // console.error("JSON:", error); //  JSON 
              // this.$toast.error("");
              this.transactions = []; // 
            }
          } else {
            // console.warn("");
            this.transactions = []; // 
          }
          // this.transactions = JSON.parse(response.data.data);
        })
        .catch(error => {
          this.$toast.error(''+error);
        })
        .finally(() => {
          // this.$toast.info(this.transactions.length.toString());
          if (this.transactions.length === 0) {
          this.ifNothing = true; 
          //  this.$toast.info("....");
          } else {
            this.ifNothing = false;
          }
        });
    }
  },
  beforeDestroy() {
    // 
    if (this.timer) {
      clearInterval(this.timer);
    }
  }
};
</script>
<style scoped>
hr {
  border: none; /*  */
  height: 2px; /*  */
  background-color: #333; /*  */
}
.centered-container {
  display: flex !important;
  justify-content: center !important; /*  */
  align-items: center !important; /*  */
}
</style>
